header {
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
header ul {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80vw;
  height: 100%;
  flex-direction: row;
}
header ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: var(--text-color);
  width: 10rem;
}
header ul li:hover {
  color: var(--text-color-light);
  background-color: var(--secondary-color);
}

.hero__section {
  width: 100%;
  height: auto;
}
.hero__section .title__side {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25vh;
  width: 100%;
}
.hero__section .title__side h2 {
  font-size: 5rem;
}
.hero__section .title__side h2 strong {
  color: var(--text-color-dark);
}
.hero__section .banner__holder {
  width: 100%;
  height: 50vw;
  max-height: 65vh;
  background-size: cover;
  background-image: url("/res/slider1.jpg");
}

.card__section {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 55vh;
  padding: 2rem;
}
.card__section :nth-child(1) .img__holder {
  background-image: url(/res/pic1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section :nth-child(2) .img__holder {
  background-image: url(/res/pic2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section :nth-child(3) .img__holder {
  background-image: url(/res/pic3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section .card {
  width: 25vw;
}
.card__section .card .subtitle__holder {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__section .card .img__holder {
  height: 20vh;
}
:root {
  --primary-color: #FFFFFF;
  --secondary-color: #662f32;
  --tertiary-color: #645955;
  --text-color: #000000;
  --text-color-light: #f5f5f5;
  --text-color-dark: #662f32;
}

* {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}